<template>
  <!--  学号：user_id，姓名：user_name，密码：user_pwd，-->
  <!-- r密码：rpassword，手机号：user_phone，性别：user_gender -->

  <el-container >
    <el-header class="header-login">
      <div class="login">
        校园淘登录页面
      </div>
    </el-header>
      <div class="loginContainer">
        <el-form  :model="numberValidateForm" ref="numberValidateForm"
                  label-width="200px" :rules="rules"  class="loginForm">
          <el-form-item >
            <h2>欢迎登录</h2>
          </el-form-item>
          <el-form-item label="手机号" prop="user_id">
            <el-input placeholder="手机号必须为数字值" type="user_id" class="input_userID" v-model.number="numberValidateForm.user_id" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="密码" prop="user_pwd">
            <el-input placeholder="密码不能为空" type="password" class="input_userID" v-model="numberValidateForm.user_pwd" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('numberValidateForm')">登录</el-button>
            <el-button @click="resetForm('numberValidateForm')">重置</el-button>
          </el-form-item>
          <el-form-item>
            <a href="#/register" style="width: auto">还没有注册的小伙伴点击注册</a>
          </el-form-item>
        </el-form>
      </div>

  </el-container>

</template>

<script>
    import axios from "axios";

    export default {
      name:'Login',
      data() {
        return {
          numberValidateForm: {
            user_id: '',
            user_pwd: '',
          },
          rules: {
            user_id: [
              { required: true, message: '手机号不能为空'},
              { type: 'number', message: '手机号必须为数字值'},
            ],
            user_pwd: [
              { required: true, message: '密码不能为空'},
            ],
          }
        }
      },
      methods: {
        LoginTheApp: function () {

          axios({
            method: 'post',
            url: '/XYT/login',
            data: {
              user_id: this.user_id,
              user_pwd: this.user_pwd,
            }
          }).then(function (response) {
            this.$router.push({path: '/home', query: {isNotLogin: false}});
          }).catch(function (error) {
            alert("登录失败" + console.log(error))
          });
        },
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              this.LoginTheApp();
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        },
      }
}
</script>

<style scoped>
.loginContainer {
  padding-top: 11px ;
  position: fixed;
  top: 42%;
  left: 50%;
  width:50%;
  height: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
}
.loginForm {
  margin-top:1px;
  text-align: left;
}
.input_userID {
  width: 300px;
}
.header-login {
  margin: 0;
  padding: 0;
  font-size: 25px;
  font-weight: bold;
  color: #333;
  background-color: #B3C0D1;
}
.login {

  padding-top: 10px;
}
</style>



