<template>
  <el-container class="registerContainer">
  <el-header class="header-register">
    <div class="regi">
      校园淘注册页面
    </div>
  </el-header>
    <el-main >
      <div class="form_register">
        <el-form  :model="numberValidateForm" ref="numberValidateForm"
                  label-width="200px"  :rules="rules" class="registerForm">
          <el-form-item >
            <h2 style="padding-left: 85px">欢迎注册</h2>
          </el-form-item>
          <!--            <el-form-item label="学号" prop="user_id">-->
          <!--              <el-input placeholder="学号必须为数字值" type="user_id" style="width: 300px" v-model.number="numberValidateForm.user_id" autocomplete="off"></el-input>-->
          <!--            </el-form-item>-->
          <el-form-item label="昵称" prop="user_name">
            <el-input placeholder="昵称不能为空" type="user_name" style="width: 300px" v-model="numberValidateForm.user_name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="user_pwd">
            <el-input placeholder="密码不能为空" type="password" style="width: 300px" v-model="numberValidateForm.user_pwd" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="再次输入密码" prop="rpassword">
            <el-input placeholder="密码不能为空" type="password" style="width: 300px" v-model="numberValidateForm.rpassword" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话号码" prop="user_phone">
            <el-input placeholder="电话号码必须为数字值" type="user_phone" style="width: 300px" v-model.number="numberValidateForm.user_phone" autocomplete="off"></el-input>
          </el-form-item>
          <!--            <el-form-item label="性别" prop="user_gender">-->
          <!--              <el-radio v-model="user_gender" label="男" v-model.number="numberValidateForm.user_gender" autocomplete="off">男</el-radio>-->
          <!--              <el-radio v-model="user_gender" label="女" v-model.number="numberValidateForm.user_gender" autocomplete="off">女</el-radio>-->
          <!--            </el-form-item>-->


          <el-form-item >
            <el-button type="primary" @click="submitForm('numberValidateForm')">注册</el-button>
            <el-button @click="resetForm('numberValidateForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-main>

  </el-container>
</template>

<script>
export default {
      name: "register",
      data() {
        var validatePass2 = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请再次输入密码'));
          } else if (value !== this.numberValidateForm.user_pwd) {
            callback(new Error('两次输入密码不一致!'));
          } else {
            callback();
          }
        };
        return {
          numberValidateForm: {
            // user_id: '',
            user_nick_name: '',
            user_pwd: '',
            rpassword: '',
            user_phone: '',
            // user_gender : '',
          },
          rules: {
            // user_id: [
            //   { required: true, message: '昵称不能为空'},
            //   { type: 'number', message: '昵称必须为数字值'}
            // ],
            user_name: [
              { required: true, message: '昵称不能为空'},
            ],
            user_pwd: [
              { required: true, message: '密码不能为空'},
            ],
            rpassword: [
              { validator: validatePass2, trigger: 'blur' }
            ],
            user_phone: [
              { required: true, message: '电话号码不能为空'},
              { type: 'number', message: '电话号码必须为数字值'}
            ],
            // user_gender: [
            //   { type: 'boolean', message: '未选择性别'},
            // ],
          },
        }
      },
      methods:{
        toRegisterApp:function () {
          axios({
            method: 'post',
            url: '/XYT/regiser',
            data: {
              user_id: this.user_id,
              user_name: this.user_name,
              user_pwd: this.user_pwd,
              rpassword: this.rpassword,
              user_phone: this.user_phone,
              user_gender : this.user_gender,
            }
          }).then(function(response) {
            this.$router.push('/Login');
          }) .catch(function (error) {
            alert("注册失败"+console.log(error))
          });
      },
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              this.toRegisterApp();
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        }
      }
}
</script>

<style scoped>

.registerForm {
  margin-top:1px;
}

.grid-content {
  min-height: 36px;
}
.header-register {
  margin: 0;
  padding: 0;
  font-size: 25px;
  font-weight: bold;
  color: #333;
  background-color: #B3C0D1;
}
.regi {
  padding-top: 10px;
}
.form_register {
  position: fixed;
  top: 42%;
  left: 50%;
  width:50%;
  height: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
}


</style>



