<template>
  <div>
    <el-form class="Edit_form" :model="EditInfo" :rules="rules"  ref="EditInfo" label-width="100px">
      <el-form-item label="昵称" prop="user_nick_name">
        <el-input :placeholder="EditInfo.user_nick_name" type="text" v-model="EditInfo.user_nick_name"></el-input>
      </el-form-item>
      <el-form-item label="电话号码" prop="user_phone">
        <el-input :placeholder="EditInfo.user_phone" type="text" v-model="EditInfo.user_phone"></el-input>
      </el-form-item>
      <el-form-item label="地址" prop="address">
        <el-input :placeholder="EditInfo.address" type="text" v-model="EditInfo.address"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('EditInfo')">修改</el-button>
        <el-button @click="resetForm('EditInfo')">重置</el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>

import axios from "axios";

export default {
  name: "update_other",
  data() {
    return {
      EditInfo: {
        user_nick_name: '222',
        user_phone: '222',
        address: '22',
      },
      rules: {
        user_phone: [
          { required: true, message: '手机号不能为空'},
          { type: 'number', message: '手机号必须为数字值'},
        ],
        user_nick_name: [
          { required: true, message: '昵称不能为空'},
        ],
        address: [
          { required: true, message: '地址不能为空'},
        ],
      },
    }
  },
  methods: {
    update_info:function() {
      axios({
        method: 'post',
        url: '/XYT/update_info',
        data: {
          user_nick_name: this.EditInfo.user_nick_name,
          user_phone: this.EditInfo.user_phone,
          address: this.EditInfo.address,
        }
      }).then(function (response) {
        alert("修改成功");
      }).catch(function (error) {
        alert("修改失败" + console.log(error))
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          update_info();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  beforeMount() {
    // axios.get("")
    // .then(responese => (this.EditInfo = responese.data))
    // .catch(function (error) {
    //     console.log(error);
    //   });
    this.EditInfo.user_phone = 5465;
    this.EditInfo.user_nick_name = "sdfsa";
    this.EditInfo.address = "dd";
  }
}
</script>

<style scoped>

</style>



