<template>
  <el-container>
    <el-header>添加商品</el-header>
    <el-main>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="test-ruleForm">
        <el-form-item label="商品名称" class="goodName" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="商品类型" prop="typeOfgood">
          <el-select v-model="ruleForm.typeOfgood" placeholder="请选择商品类型">
            <el-option label="生活" value="生活"></el-option>
            <el-option label="学习" value="学习"></el-option>
            <el-option label="其它" value="其它"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品数量" class="goodNum" prop="numbers">
          <el-input v-model="ruleForm.numbers"></el-input>
        </el-form-item>
        <el-form-item label="商品价格" class="goodPrice" prop="price">
          <el-input v-model="ruleForm.price"></el-input>
        </el-form-item>
        <el-form-item label="选择校区" prop="typeOfcampus">
          <el-select v-model="ruleForm.typeOfcampus" placeholder="请选择选择校区">
            <el-option label="余家头" value="余家头"></el-option>
            <el-option label="马房山" value="马房山"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="上传图片" prop="pic">
          <el-upload class="upload-demo"  action="" :auto-upload="false" ref="upload" multiple>
            <!-- action为上次图片到xxx地方,必填 -->
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>

<script>
  import axios from "axios";

  export default {
      name: "upGoodsView",
    data() {
      return {
        ruleForm: {
          name: '',
          numbers: '',
          typeOfgood: '',
          price: '',
          typeOfcampus: '',
          pic: '',
        },
        rules: {
          name: [
            { required: true, message: '请输入商品名称', trigger: 'blur' },
            { min: 1, max: 10, message: '长度在 1 到 10 个字符', trigger: 'blur' }
          ],
          typeOfgood: [
            { required: true, message: '请选择商品类型', trigger: 'change' }
          ],
          numbers: [
            { required: true, message: '请输入商品数量', trigger: 'blur' },
          ],
          price: [
            { required: true, message: '请输入商品价格', trigger: 'blur' },
          ],
          typeOfcampus: [
            { required: true, message: '请选择校区', trigger: 'change' },
          ],
          pic :[
            {required: true, message: '请上次图片', trigger: 'change'},
          ]
        }
      };
    },
    methods: {
      // uploadInfo:function() {
      //   axios
      // },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
            uploadInfo();
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      submitUpload:function () {
        this.$refs.upload.submit();
      }
    },
    beforeMount() {
        //获取userId
    }
  }
</script>

<style scoped>
.goodName {
  width: 500px;
}
.goodNum {
  width: 500px;
}
.goodPrice {
  width: 500px;
}
</style>



