<template>
  <el-container>
    <el-header class="shopcar_header">
      <div style="padding-top: 10px">{{user_name}}的购物车</div>
    </el-header>
    <el-main>
      <div>
      <el-table :data="tableData"  style="width: 100%" >
        <el-table-column prop="goods_name" label="商品名称" width="180">
        </el-table-column>
        <el-table-column prop="stock" label="剩余数量">
        </el-table-column>
        <el-table-column prop="price" label="商品价格">
        </el-table-column>
        <el-table-column prop="input_time" label="日期" width="180">
        </el-table-column>

        <el-table-column  label="购买">
          <el-button type="text" @click="buy">点击购买</el-button>
        </el-table-column>
        <el-table-column  label="删除" width="100">
          <el-button type="text" @click="deleteIt()">删除</el-button>
        </el-table-column>
      </el-table>
      <div class="total_price">
        总价：￥{{this.tableData.total_price}}
      </div>
    </div>
    </el-main>
  </el-container>


</template>

<script>
export default {
  name: "shop_car",
  data() {
    return {
      user_name: 'nan',
      srcList: [require('../assets/good.png'),require('../assets/OIP.jpg')],
      url: require('../assets/good.png'),
      tableData: [{
        cart_id:parseInt(2),
        user_id:parseInt(2),
        user_name: '难',
        price: parseFloat(2),
        goods_id: parseInt(2),
        goods_name: '2',
        stock: parseInt(2),
        total_price: parseFloat(2),
        input_time: Date(2020-1-1),
      },],
    }
  },
  methods:{
    buy:function () {
      this.$router.push('/pay');
    },
    deleteIt:function () {
      this.tableData.pop();
    },
  },
  beforeMount() {
    //请求购物车数据
  }
}
</script>

<style scoped>
.shopcar_header {
  margin: 0;
  padding-left: 0;
  color: black;
  background-color: #B3C0D1;
  font-size: 25px;
}

.total_price {
  color: #ff4400;
  font-weight: bold;
  font-size: larger;

}
</style>



