<template>
  <el-container>
    <el-form :model="EditPwd" status-icon :rules="rules" ref="EditPwd" label-width="100px" class="ruleForm">
      <el-form-item label="新密码" prop="user_pwd">
        <el-input type="password" v-model="EditPwd.user_pwd" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="re_user_pwd">
        <el-input type="password" v-model="EditPwd.re_user_pwd" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('EditPwd')">提交</el-button>
        <el-button @click="resetForm('EditPwd')">重置</el-button>
      </el-form-item>
      </el-form>
  </el-container>
</template>

<script>
import axios from "axios";

export default {
  name: "update_pwd",
  data() {

    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.EditPwd.re_user_pwd !== '') {
          this.$refs.EditPwd.validateField('re_user_pwd');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.EditPwd.user_pwd) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      EditPwd: {
        user_pwd: '',
        re_user_pwd: '',
      },
      rules: {
        user_pwd: [
          { validator: validatePass, trigger: 'blur' }
        ],
        re_user_pwd: [
          { validator: validatePass2, trigger: 'blur' }
        ],
      }
    };

  },
  methods: {
    update:function() {
      axios({
        method: 'post',
        url: '/XYT/update_pwd',
        data: {
          user_pwd: this.EditPwd.user_pwd,
        }
      }).then(function (response) {
        alert("修改成功");
      }).catch(function (error) {
        alert("修改失败" + console.log(error))
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.update();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style scoped>

</style>



