      PurchaseGood:function () {
        this.$router.push({path: '/pay', query: {goods_id: this.goods.goods_id}});
      },
      addCart:function () {
        axios({
          method: 'post',
          url: '/XYT/addCart',
          data: {
            user_id: this.user_id,
            goods_id: this.goods_id,
          }
        }).then(function (response) {
          alert("加入成功");
        }).catch(function (error) {
          alert("加入购物车失败" + console.log(error));
        });
      },
    },
