<template>
  <el-container>

    <el-dropdown trigger="click" placement="bottom">
      <el-avatar  :size="250" fit="fill" :src="url0"></el-avatar>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item >
          <el-upload class="upload-demo"  ref="upload" :url1 = 'url1' :auto-upload="false"  :on-change="imgPreview"
            action="https://localhost:8080/xyt/src/assets" multiple>
            <el-button slot="trigger" size="small" type="primary" @click="uploadPic">选取文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </el-container>
</template>

<script>
export default {
  name: "update_avatar",
  data() {
    return {
      url0: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
      url1: '',
    }
  },
  methods: {
    uploadPic:function () {
      this.url0 = this.url1;
    },
    submitUpload:function () {
      this.$refs.upload.submit();
      //上传后获取 图片给url1 执行UploadPic函数
    },
    imgPreview (file) {
      let fileName = file.name;
      console.log(file);
      let regex = /(.jpg|.jpeg|.gif|.png|.bmp)$/;
      if (regex.test(fileName.toLowerCase())) {
        this.url0 = file.url;
        console.log(this.url0);
      } else {
        this.$message.error('请选择图片文件');
      }
    },
  }

}
</script>

<style scoped>

</style>



