<template>
    <div class="goods" >
      <div class="back" v-for="a in 16">
        <div class="learn_goods"  @click="toAll">
          <div class="learn_goods_pic" >
            <el-image
              style="width: 225px; height: 200px ; vertical-align: middle;"
              :src="url"
              :preview-src-list="srcList">
            </el-image>
          </div>
          <div class="learn_goods_price"><span >¥{{price}}</span></div>
          <div class="learn_goods_addr" >
            <span class="learn_goods_name"  style="text-align: right;">商品名称</span>
          </div>
          <div class="learn_goods_date" >
            <span >剩余数量：{{num}}</span>
          </div>

        </div>
      </div>

    </div>
</template>

<script>
export default {
  name: "life",
  data() {
    return {
      num: 2,
      goods: [],
      price: 118,
      url: require('../assets/good.png'),
      srcList: [
        require('../assets/good.png'),
      ]
    };
  },
  methods: {
    toAll:function () {

    }
  },
}
</script>

<style scoped>
.goods {
  width: 955px;
  flex:1;
  background:#f5f5f5;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: flex-start;
  margin-left: 50px;
  margin-top: 0;

}
.back {
  background-color: white;
  width: 230px;
  height: 280px;
  margin: 4px;
}
.learn_goods {

  width:225px;
  height: 275px;

}
.learn_goods_addr {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.learn_goods_date {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.learn_goods_price {
  color: orange;
  font-weight: bolder;
}
</style>



