import Vue from 'vue'
import Router from 'vue-router'
import home from "../components/home";
import Login from "../components/Login";
import register from "../components/register";
import pay from "../components/pay";
import bills from "../components/bills";
import shop_car from "../components/shop_car";
import upGoodsView from "../components/upGoodsView";
import other_good_view from "../components/other_good_view";
import learn from "../components/learn";
import life from "../components/life";
import goodMain from "../components/goodMain";
import editView from "../components/editView";
import update_avatar from "../components/update_avatar";
import update_pwd from "../components/update_pwd";
import update_other from "../components/update_other";
import authentication from "../components/authentication";

Vue.use(Router)

export default new Router({

  routes: [
    {path: '/', redirect:'/home' ,},
    {path: '/home', component: home,
      children:
        [ {path:'learn',component:learn},
          {path:'life',component: life},
          {path: 'other_good_view',component: other_good_view,}]},
    {path: '/Login',component: Login,},
    {path: '/register',component: register},
    {path:'/pay',component: pay},
    {path:'/bills',component: bills},
    {path:'/shop_car',component: shop_car},
    {path:'/upGoodsView',component: upGoodsView},
    {path:'/goodMain',component: goodMain},
    {path:'/pay',component: pay},
    {path: '/editView',component: editView,
      children: [ {path: '/update_avatar',component: update_avatar},
                  {path: '/update_pwd',component: update_pwd},
                  {path: '/update_other',component: update_other,}]},
    {path:'/authentication',component: authentication}
  ]
})



